.Container {
 width: 100%;
 height: 100%;
  }
  .Content {
    display: block;
    justify-content: center;
    text-align: center;
  }
  .H1 {
    color: rgb(35, 45, 101);
  }
  .SearchBar {
  }
  .Movies {
    margin: auto;
      display: grid;
    grid-template-columns: repeat(2, minmax(400px, 1fr));
    grid-gap: 100px;
    padding: 50px;
    width: 80%;
    padding-top: 70px;
    text-align: left;
  }

  .Loading {
    display: flex;
    text-align: center;
   vertical-align: middle;
    font-size: xx-large;
    color: rgb(129, 135, 141);
  }
  